.html {
  width: 90%;
  -webkit-animation: html 3s;
          animation: html 3s;
}
@-webkit-keyframes html {
  0% {
    width: 0%;
  }
  90% {
    width: 90%;
  }
}
@keyframes html {
  0% {
    width: 0%;
  }
  90% {
    width: 90%;
  }
}

.js {
  width: 80%;
  -webkit-animation: js 6s;
          animation: js 6s;
}
@-webkit-keyframes js {
  0% {
    width: 0%;
  }
  90% {
    width: 80%;
  }
}
@keyframes js {
  0% {
    width: 0%;
  }
  90% {
    width: 80%;
  }
}

.react {
  width: 60%;
  -webkit-animation: react 9s;
          animation: react 9s;
}
@-webkit-keyframes react {
  0% {
    width: 0%;
  }
  90% {
    width: 60%;
  }
}
@keyframes react {
  0% {
    width: 0%;
  }
  90% {
    width: 60%;
  }
}

.nodejs {
  width: 50%;
  -webkit-animation: nodejs 12s;
          animation: nodejs 12s;
}
@-webkit-keyframes nodejs {
  0% {
    width: 0%;
  }
  90% {
    width: 50%;
  }
}
@keyframes nodejs {
  0% {
    width: 0%;
  }
  90% {
    width: 50%;
  }
}
